<template>
  <div>
    <page-title/>

    <div class="panel">
      <div class="table-responsive">
        <table class="table">
          <thead>
          <tr class="text-center">
            <th class="text-start">عنوان مقاله</th>
            <th>تاریخ ثبت</th>
            <th>تاریخ اخرین بروز رسانی</th>
            <th>&</th>
          </tr>
          </thead>

          <tbody>
          <tr v-for="x in posts" :key="x.pstId" class="text-center">
            <td class="text-start">{{ x.pstTitle }}</td>
            <td>{{ $filters.localDate(x.createdDate) }}</td>
            <td>{{ $filters.localDate(x.updatedData) }}</td>
            <td>
              <router-link :to="`/cms/posts/${x.pstId}/edit`" class="btn btn-info text-white btn-sm p-1">
                <span class="fa fa-pen me-1"></span>
                ویرایش
              </router-link>
            </td>
          </tr>
          </tbody>
        </table>
      </div>
    </div>

  </div>
</template>

<script>
import PageTitle from '../../tools/PageTitle'

export default {
  name: 'Posts',
  components: { PageTitle },
  data: () => ({
    working: false,
    filters: {},
    posts: []
  }),
  methods: {
    retrievePosts(page = 1) {
      this.working = true
      this.$fetch('/post', 'get', {
        ...this.filters,
        page
      })
          .then(x => this.posts = x.data)
          .finally(() => this.working = false)
    }
  },
  mounted() {
    this.retrievePosts()
  }
}
</script>

